/* ShowList.css */

.show-list-container {
    text-align: center;
    padding: 20px;
}

.show-card-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.show-card {
    width: calc(22.22% - 20px);
    margin: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
    position: relative;
}

/* Update the media query for smaller screens */
@media (max-width: 600px) {
    .show-card {
        width: calc(70% - 20px);
    }
}

.show-card:hover {
    transform: scale(1.05);
}

.show-image {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
}

.card-details {
    padding: 10px;
    text-align: left;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.show-card:hover .card-details {
    opacity: 1;
}

.show-name {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 2px;
}

.show-language {
    margin-bottom: 10px;
    margin-top: -10px;
}

.details-button {
    display: inline-block;
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.3s ease-in-out;
}

.show-card:hover .details-button {
    background-color: #0056b3;
}